import React from 'react';
import Breadcrumb from 'Elements/Breadcrumbs';
import { Button, Card, Col, Image, Row, Typography } from 'antd';
import { useNavigate } from 'react-router-dom';

import XgbtTrain from 'Assets/images/xgb/xgb_train.png'
import XgbtTest from 'Assets/images/xgb/xgb_test.png'
import XgbtFeature from 'Assets/images/xgb/xgb_features_importance.png'
import XgbtCM from 'Assets/images/xgb/xgb_cm.png'
import XgbtCF from 'Assets/images/xgb/xgb_cf.png'
import Helmet from 'react-helmet';

function Xgb() {
   const navigate = useNavigate()
   const TITLE = 'Xtreme Gradient Boost | EEG Classification'

   const handleNavigate = (url) => {
      if (url === 'predict') navigate('/predict')
      else if (url === 'back') navigate('/predict-eeg')
   }

   const breadcrumb = [
      {
         pageTitle: "Predict EEG",
         pageHref: "/predict-eeg"
      },
      {
         pageTitle: "Xtreme Gradient Boost",
         pageHref: "/Xgb"
      }
   ]

   const { Title } = Typography;

   return (
      <>
         <Helmet>
            <title>{TITLE}</title>
         </Helmet>
         <Breadcrumb data={breadcrumb} />
         <Title
            style={{
               textAlign: 'center',
               marginTop: '30px'
            }}
         >Xtreme Gradient Boost</Title>
         <div style={{
            textAlign: 'center',
            marginBottom: '30px'
         }}>
            <Button type="primary" onClick={() => handleNavigate('predict')}>Start Prediction</Button>
            <Button type="primary" onClick={() => handleNavigate('back')} style={{ marginLeft: '5px' }}>Back</Button>
         </div>

         <Row
            style={{
               textAlign: 'center',
               margin: '10px auto'
            }}>
            <Col span={12}>
               <Row>
                  <Col span={12}>
                     <Title
                        level={3}
                        style={{
                           margin: '10px auto'
                        }}
                     >Classification Report for Data Train</Title>
                     <Image
                        src={XgbtTrain}
                        width={'100%'}
                     />
                  </Col>
                  <Col span={12}>
                     <Title
                        level={3}
                        style={{
                           textAlign: 'center',
                           margin: '10px auto'
                        }}
                     >Classification Report for Data Test</Title>
                     <Image
                        src={XgbtTest}
                        width={'100%'}
                     />
                  </Col>
               </Row>
            </Col>
            <Col span={9} offset={1}>
               <Card
                  style={{
                     borderRadius: '20px',
                  }}>
                  <Title
                     level={2}
                  >Information About This Model</Title>
                  <Title level={4} style={{
                     textAlign: 'left',
                     marginTop: '-15px'
                  }}>Xtreme Gradient Boost</Title>
                  <Title level={5} style={{
                     textAlign: 'left',
                     marginTop: '5px'
                  }}>Parameter</Title>
                  <ul style={{
                     textAlign: 'left'
                  }}>
                     <li>N estimators: <b>200</b></li>
                     <li>Learning Rate: <b>0.3</b></li>
                     <li>Max Depth: <b>40</b></li>
                     <li>gamma: <b>0</b></li>
                  </ul>
                  <Title level={5} style={{
                     textAlign: 'left',
                     margin: 0
                  }}>Model Accuracy : 87%</Title>
                  <Title level={5} style={{
                     textAlign: 'left',
                     margin: 0
                  }}>Cross Validation : 5 Fold</Title>
               </Card>
            </Col>
         </Row>

         <Row>
            <Card>
               <div style={{
                  display: 'flex',
                  justifyContent: 'center',
                  margin: '10px auto'
               }}>
                  <Title
                     level={2}
                     style={{
                        textAlign: 'center'
                     }}
                  >Parameter Grid</Title>
                  <Image
                     src={XgbtCF}
                     width={'100%'}
                  />
               </div>
            </Card>
         </Row>

         <Row>
            <Card
               style={{
                  display: 'flex',
                  justifyContent: 'center',
                  margin: '10px auto'
               }}>
               <Title
                  level={2}
                  style={{
                     textAlign: 'center'
                  }}
               >Confusion Matrix </Title>
               <Image
                  src={XgbtCM}
                  width={'100%'}
               />
            </Card>
         </Row>

         <Row>
            <Card
               style={{
                  display: 'flex',
                  justifyContent: 'center',
                  margin: '30px auto'
               }}>
               <Title
                  level={2}
                  style={{
                     textAlign: 'center'
                  }}
               >Features Importance</Title>
               <Image
                  src={XgbtFeature}
                  width={'100%'}
               />
            </Card>
         </Row>
      </>
   )
}

export default Xgb