import React, { useContext, useState } from 'react'
import { Button, Card, Typography } from 'antd'
import Breadcrumbs from 'Elements/Breadcrumbs'
import { PredictContext } from 'Context/PredictContext';
import csvInput from 'Assets/csv/Bulk_Input.csv'
import Helmet from 'react-helmet';

function UploadCSV() {
   const { uploadFile, setUploadFile, functions } = useContext(PredictContext)
   const { bulkPredict } = functions
   const [method, setMethod] = useState('Select Method')
   const [showInputs, setShowInputs] = useState(false)
   const TITLE = 'Upload CSV | EEG Classification'

   const breadcrumb = [
      {
         pageTitle: "Predict EEG",
         pageHref: "/predict-eeg"
      },
      {
         pageTitle: "Predict",
         pageHref: "/predict"
      },
      {
         pageTitle: "Upload CSV",
         pageHref: "/upload-csv"
      },
   ]

   const handleFileChange = (e) => {
      setUploadFile({ ...uploadFile, file: e.target.files[0] })
   };

   const handleChange = (e) => {
      setUploadFile({ ...uploadFile, method: e.target.value })
      var SELECT_DEFAULT = document.getElementById("DEFAULT_SELECT");
      if (SELECT_DEFAULT) SELECT_DEFAULT.remove();

      setShowInputs(true)
      switch (e.target.value) {
         case 'random-forest':
            setMethod('Random Forest');
            return;
         case 'k-nearest-neigbor':
            setMethod('K Nearest Neighbor');
            return;
         case 'XGboost':
            setMethod('Xtreme Gradient Boost');
            return;
         default:
            setMethod('Pick a Method');
            return;
      }
   }

   const handleSubmit = (e) => {
      e.preventDefault()
      bulkPredict()
   }

   const buttonStyle = {
      display: 'block',
      background: '#3479ce',
      border: 'none',
      borderRadius: 0,
      width: '100px',
      boxSizing: 'border-box',
      padding: '2px 10px',
      backgroundColor: '#4493c7',
      transition: 'all 0.6s',
      color: '#FFF',
      fontSize: '15px',
      verticalAlign: 'middle',
      textTransform: 'uppercase',
      margin: '0 auto',
   }

   const input = {
      position: 'relative',
      overflow: 'hidden',
      lineHeight: '20px',
      padding: '5px',
      margin: '10px',
      boxSizing: 'border-box',
      fontSize: '15px',
      verticalAlign: 'middle',
      width: '300px',
      border: '2px solid #dbdbdb',
      borderRadius: '10px',
      height: 'calc(2em + 0.75rem + 2px)',
   }

   const { Title } = Typography;

   return (
      <>
         <Helmet>
            <title>{TITLE}</title>
         </Helmet>
         <Breadcrumbs data={breadcrumb} />

         <div
            style={{
               display: 'flex',
               justifyContent: 'space-between',
               margin: '30px 30px '
            }}
         >
            <Title>Upload CSV</Title>
            <Button type='primary'><a href={csvInput} download>Click to download Sample CSV</a></Button>
         </div>

         <div
            style={{
               display: 'flex',
               justifyContent: 'center',
            }}
         >
            <Title level={3}>Method : {method}</Title>
         </div>

         <div
            style={{
               display: 'flex',
               justifyContent: 'center',
            }}
         >
            <Card>
               <form onSubmit={handleSubmit}>
                  <select name='method' onChange={handleChange} style={input}>
                     <option value="" id='DEFAULT_SELECT'>Select a method</option>
                     <option value="random-forest">Random Forest</option>
                     <option value="k-nearest-neigbor">K Nearest Neigbor</option>
                     <option value="XGboost">Xtreme Gradient Boost</option>
                  </select>
                  <br />

                  {showInputs &&
                     <>

                        <input type="file"
                           style={input}
                           id="file"
                           accept=".csv"
                           onChange={handleFileChange} required /><br />

                        <input type="submit" style={buttonStyle} />
                     </>
                  }
               </form>
            </Card>
         </div>

      </>
   );
}

export default UploadCSV;
