
import axios from "axios";
import React, { useState, createContext } from "react";
import Electrode from 'Utils/getElectrode'
import getCondition from 'Utils/getCondition'
import { useNavigate } from "react-router-dom";
import Cookies from 'js-cookie'

export const PredictContext = createContext();

export const PredictProvider = props => {
   const [predictList, setPredictList] = useState([])
   const navigate = useNavigate()
   const [currentId, setCurrentId] = useState(null)
   const [electrodes, setElectrodes] = useState(Electrode)
   const [uploadFile, setUploadFile] = useState({
      'method': '',
      'file': null
   });

   const electodeName = [
      'AF3', 'F7', 'F3', 'FC5', 'T7', 'P7', 'O1', 'O2', 'P8', 'T8', 'FC6', 'F4', 'F8', 'AF4'
   ]

   const [predictResultList, setPredictResultList] = useState({});

   const [singleResult, setSingleResult] = useState({
      text: '',
      word: '',
      condition: '',
      method: '',
      electrodes: {}
   })

   const [singlePredictData, setSinglePredictData] = useState({
      text: '',
      word: '',
      condition: '',
      method: '',
      electrodes: {}
   })

   const fetchData = async () => {
      const result = await axios.get(`http://localhost:8000/api/predict`)

      setPredictList(result.data.map((x) => {
         return { ...x }
      }))
   }

   const deleteData = async (id) => {
      const csrfToken = Cookies.get('csrftoken')
      await axios.delete(`http://localhost:8000/api/predict/${id}`, {
         headers: {
            'X-CSRFToken': csrfToken
         }}).then(() => {
         let newList = predictList.filter(el => { return el.id !== id })
         setPredictList(newList)
      })

      alert(`predictive data with id = ${id} deleted successfully`)
   }

   const fetchSingleData = async (id) => {
      const result = await axios.get(`http://localhost:8000/api/predict/${id}`)

      try {
         const data = await result.data[0]

         let electrodes = data
         let word = data.word
         let condition = data.condition
         let method = data.method

         let column = ['word', 'condition', 'method', 'created', 'id']
         column.forEach(e => delete electrodes[e]);
         setSinglePredictData({
            word: word,
            condition: condition,
            text: getCondition(condition, word),
            method: method,
            electrodes: electrodes
         })
      } catch (error) {
         console.log('Error happened here!')
         console.error(error)
      }

      navigate('/predict-details');
   }

   const singlePredict = async (electrodes, method) => {
      const csrfToken = Cookies.get('csrftoken')

      let body = {
         'method': method,
         'electrodes': electrodes,
      }

      let res = await fetch(`http://localhost:8000/api/predict`, {
         method: 'POST',
         headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'X-CSRFToken': csrfToken
         },
         body: JSON.stringify(body)
      })

      try {
         const data = await res.json()
         let word = data.word
         let condition = data.condition
         let method = data.method

         setSingleResult({
            word: word,
            condition: condition,
            text: getCondition(condition, word),
            method: method,
            electrodes: electrodes
         })
      } catch (error) {
         console.log('Error happened here!')
         console.error(error)
      }
   }

   const bulkPredict = async () => {
      const csrfToken = Cookies.get('csrftoken')
      let form_data = new FormData();
      form_data.append('file', uploadFile.file, uploadFile.file.name);
      form_data.append('method', uploadFile.method);
      let data
      let url = 'http://localhost:8000/api/predict';
      setPredictResultList({ onProgress: 1 })
      axios.post(url, form_data, {
         headers: {
            'content-type': 'multipart/form-data',
            'X-CSRFToken': csrfToken
         }
      })
         .then(res => {
            data = res.data
            if (data.classCount) {
               setUploadFile({
                  'method': '',
                  'file': null
               })
               let bulkResult = data.classCount
               let result = []
               for (let i = 0; i < bulkResult.length; i++) {
                  let singleRes = bulkResult[i]
                  result.push({
                     text: getCondition(singleRes[1], singleRes[0]),
                     word: singleRes[0],
                     condition: singleRes[1],
                  })
               }

               setPredictResultList({
                  method: uploadFile.method,
                  onProgress: 1,
                  bulkResult: result
               })

               navigate('/multiple-predict-result')
            } else {
               let electrodes = data
               let word = data.word
               let condition = data.condition
               let method = data.method

               let column = ['word', 'condition', 'method', 'created', 'id']
               column.forEach(e => delete electrodes[e]);
               setSingleResult({
                  word: word,
                  condition: condition,
                  text: getCondition(condition, word),
                  method: method,
                  electrodes: electrodes
               })
               navigate('/predict-result')
            }
         })
         .catch(err => console.log(err))
   }

   const blankSingleResult = () => {
      setSingleResult({
         onProgress: '0',
         text: '',
         word: '',
         condition: '',
         method: '',
         electrodes: {}
      })
   }

   const functions = {
      fetchData, singlePredict, bulkPredict, blankSingleResult, fetchSingleData, deleteData
   }

   return (
      <PredictContext.Provider value={{
         predictList, setPredictList, currentId, setCurrentId, electrodes, setElectrodes, predictResultList, setPredictResultList, uploadFile, setUploadFile, singleResult, setSingleResult, electodeName, singlePredictData, setSinglePredictData, functions
      }}>
         {props.children}
      </PredictContext.Provider>
   );
};