import { PredictContext } from 'Context/PredictContext';
import Breadcrumbs from 'Elements/Breadcrumbs';
import React, { useContext } from 'react';
import { Button, Card, Col, Row, Typography } from 'antd'
import { useNavigate } from 'react-router-dom';
import Helmet from 'react-helmet';

function PredictResult() {
   const { singleResult, electrodes, electodeName, functions } = useContext(PredictContext)
   const { blankSingleResult } = functions
   const navigate = useNavigate()
   const TITLE = 'Predict Result | EEG Classification'

   const handleNavigate = () => {
      blankSingleResult()
      navigate('/predict')
   }

   const breadcrumb = [
      {
         pageTitle: "Predict EEG",
         pageHref: "/predict-eeg"
      },
      {
         pageTitle: "Input Features",
         pageHref: "/predict"
      },
      {
         pageTitle: "Classification Report",
         pageHref: ""
      }
   ]

   const GridStyle = { background: '#bce2ea', padding: '8px 0', marginBottom: '10px', borderRadius: '10px' };
   const { Title } = Typography;
   const LineStyle = {
      display: 'flex',
      justifyContent: 'space-between',
      margin: '30px 30px 0 30px '
   }
   console.log(singleResult.method);
   return (
      singleResult.method === '' ?
         <>
            <Helmet>
               <title>{TITLE}</title>
            </Helmet>
            <div style={{
               display: 'flex',
               justifyContent: 'center',
               marginBottom: '20px '
            }}>
               <Title level={4} style={{ margin: '0' }}>
                  Data Tidak Ditemukan
               </Title>
            </div>
         </> : <>
            <Helmet>
               <title>{TITLE}</title>
            </Helmet>
            <Breadcrumbs data={breadcrumb} />

            <div style={LineStyle}>
               <Title>Classification Report</Title>
               <Button type='primary' onClick={handleNavigate}>Predict Again</Button>
            </div>

            <table style={{
               width: '300px',
               margin: '0 0 30px 50px'
            }}>
               <tbody>
                  <tr>
                     <td><Title level={4}>Method</Title></td>
                     <td><Title level={4}> : {singleResult.method.replace('-', ' ').toLowerCase().split(' ').map(s => s.charAt(0).toUpperCase() + s.substring(1)).join(' ')}</Title></td>
                  </tr>
                  <tr>
                     <td><Title level={4}>Word</Title></td>
                     <td><Title level={4}> : {singleResult.word}</Title></td>
                  </tr>
                  <tr>
                     <td><Title level={4}>Condition</Title></td>
                     <td><Title level={4}> : {singleResult.condition}</Title></td>
                  </tr>
               </tbody>
            </table>

            <div style={{
               display: 'flex',
               justifyContent: 'center',
               margin: '30px 30px '
            }}>
               <Title level={2} style={{ margin: '0' }}>
                  "{singleResult.text.toLowerCase().split(' ').map(s => s.charAt(0).toUpperCase() + s.substring(1)).join(' ')}"
               </Title>
            </div>

            <div style={{
               display: 'flex',
               justifyContent: 'center',
               marginBottom: '20px '
            }}>
               <Title level={4} style={{ margin: '0' }}>
                  Electrode Waves
               </Title>
            </div>

            <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
               {electrodes.map((electrode, indexElectrode) => (
                  <Col span={6} key={indexElectrode}>
                     <Card
                        style={GridStyle}>
                        <h2>{electodeName[indexElectrode]}</h2>
                        {Object.keys(electrode).map((item, index) => (
                           <h3 key={index}>{item}: {singleResult.electrodes[item]}</h3>
                        ))}
                     </Card>
                  </Col>
               ))}
            </Row>

         </>
   );
}

export default PredictResult