import { PredictContext } from 'Context/PredictContext';
import React, { useContext } from 'react';
import Breadcrumb from 'Elements/Breadcrumbs';
import { Button, Card, Col, Row, Typography } from 'antd';
import { useNavigate } from 'react-router-dom';
import Helmet from 'react-helmet';

function MultiplePredictResult() {
   const { predictResultList, functions } = useContext(PredictContext)
   const { blankSingleResult } = functions
   const { Title } = Typography;
   const navigate = useNavigate()
   const TITLE = 'Multiple Predict Result | EEG Classification'

   const handleNavigate = () => {
      blankSingleResult()
      navigate('/predict')
   }

   const Gridstyle = { background: '#bce2ea', padding: '8px 0', marginBottom: '10px', borderRadius: '10px' };
   const LineStyle = {
      display: 'flex',
      justifyContent: 'space-between',
      margin: '30px 30px 0 30px '
   }

   const breadcrumb = [
      {
         pageTitle: "Predict EEG",
         pageHref: "/predict-eeg"
      },
      {
         pageTitle: "Predict",
         pageHref: "/predict"
      },
      {
         pageTitle: "Upload CSV",
         pageHref: "/upload-csv"
      },
      {
         pageTitle: "Classification Bulk Report",
         pageHref: "/multiple-predict-result"
      },
   ]

   return (
      predictResultList.method === undefined ?
         <>
            <Helmet>
               <title>{TITLE}</title>
            </Helmet>
            <div style={{
               display: 'flex',
               justifyContent: 'center',
               marginBottom: '20px '
            }}>
               <Title level={4} style={{ margin: '0' }}>
                  Data Tidak Ditemukan
               </Title>
            </div>
         </> : <>
            <Helmet>
               <title>{TITLE}</title>
            </Helmet>
            <Breadcrumb data={breadcrumb} />
            <div style={LineStyle}>
               <Title>Classification Bulk Report</Title>
               <Button type='primary' onClick={handleNavigate}>Predict Again</Button>
            </div>

            <Row>
               <Col span={16} style={{ marginRight: '30px' }}>
                  <Card
                     style={Gridstyle}>
                     <Title level={3}>Bulk Result Classification</Title>
                     {predictResultList.bulkResult.map((item, index) => {
                        return (
                           <Card key={index}>
                              <Title level={3} style={{ margin: '2px' }}>Row {index + 1} | Result : "{item.text}"</Title>
                              <Title level={5} style={{ margin: '2px' }}>Word: {item.word} | Condition: {item.condition}</Title>
                           </Card>
                        )
                     })}
                  </Card>
               </Col>
               <Col span={7}>
                  <Card>
                     <Title level={3}>Result Information </Title>
                     <Title level={5} style={{ margin: 0 }}>Method: {predictResultList.method.replace('-', ' ').toLowerCase().split(' ').map(s => s.charAt(0).toUpperCase() + s.substring(1)).join(' ')}</Title>
                     <Title level={5} style={{ margin: 0 }}>Data Amount: {predictResultList.bulkResult.length} Data</Title>
                  </Card>
               </Col>
            </Row>
         </>
   );
}



export default MultiplePredictResult;
