import React from 'react';
import Breadcrumb from 'Elements/Breadcrumbs';
import { Button, Card, Col, Row, Typography } from 'antd';
import { useNavigate } from 'react-router-dom';
import Helmet from 'react-helmet';

function PredictEEG() {
   const navigate = useNavigate()
   const TITLE = 'Predict EEG | EEG Classification'

   const handleNavigate = (url) => {
      if (url === 'random-forest') navigate('/random-forest')
      else if (url === 'knn') navigate('/knn')
      else if (url === 'xgboost') navigate('/xgboost')
      else navigate('/predict')
   }

   const breadcrumb = [
      {
         pageTitle: "Predict EEG",
         pageHref: "/predict-eeg"
      }
   ]

   const { Title } = Typography;

   return (
      <>
         <Helmet>
            <title>{TITLE}</title>
         </Helmet>
         <Breadcrumb data={breadcrumb} />
         <Title
            style={{
               textAlign: 'center',
               marginTop: '30px'
            }}
         >EEG Classification</Title>
         <div style={{
            textAlign: 'center',
            marginBottom: '30px'
         }}>
            <Button type="primary" onClick={handleNavigate}>Start Prediction</Button>
         </div>

         <Row>
            <Col span={8}
               style={{
                  margin: '0 10px 0 20px'
               }}>
               <Card title="Random Forest" bordered={true} style={{ textAlign: 'center', padding: '5px' }}>
                  <Title level={3} style={{ marginTop: '0', textAlign: 'center' }}>Accuracy : 93%</Title>
                  <Title level={5} style={{ marginTop: '0', textAlign: 'center' }}>Parameter</Title>

                  <ol style={{
                     textAlign: 'left'
                  }}>
                     <li>N estimators: <b>500</b></li>
                     <li>Criterion: <b>entropy</b></li>
                     <li>Min samples split: <b>2</b></li>
                     <li>Min samples leaf: <b>1</b></li>
                  </ol>

                  <div style={{
                     textAlign: 'right'
                  }}>
                     <Button type="primary" ghost onClick={() => handleNavigate("random-forest")}>Learn More</Button>
                  </div>
               </Card>
            </Col>
            <Col span={7}
               style={{
                  marginRight: '10px'
               }}>
               <Card title="K Nearest Neigbor" bordered={true} style={{ textAlign: 'center', padding: '5px' }}>
                  <Title level={3} style={{ marginTop: '0', textAlign: 'center' }}>Accuracy : 74%</Title>
                  <Title level={5} style={{ marginTop: '0', textAlign: 'center' }}>Parameter</Title>

                  <ol style={{
                     textAlign: 'left'
                  }}>
                     <li>N Neigbors: <b>1</b></li>
                     <li>Weight: <b>Distance</b></li>
                     <li>P: <b>1</b></li>
                     <br />
                  </ol>

                  <div style={{
                     textAlign: 'right'
                  }}>
                     <Button type="primary" ghost onClick={() => handleNavigate("knn")}>Learn More</Button>
                  </div>
               </Card>
            </Col>
            <Col span={8}>
               <Card title="Xtreme Gradient Boost" bordered={true} style={{ textAlign: 'center', padding: '5px' }}>
                  <Title level={3} style={{ marginTop: '0', textAlign: 'center' }}>Accuracy : 87%</Title>
                  <Title level={5} style={{ marginTop: '0', textAlign: 'center' }}>Parameter</Title>

                  <ol style={{
                     textAlign: 'left'
                  }}>
                     <li>N estimators: <b>200</b></li>
                     <li>Learning Rate: <b>0.3</b></li>
                     <li>Max Depth: <b>40</b></li>
                     <li>gamma: <b>0</b></li>
                  </ol>

                  <div style={{
                     textAlign: 'right'
                  }}>
                     <Button type="primary" ghost onClick={() => handleNavigate("xgboost")}>Learn More</Button>
                  </div>
               </Card>
            </Col>
         </Row>
      </>
   );
}

export default PredictEEG;
