import React from 'react'
import { Card, Col, Image, Row, Typography } from 'antd'
import { Helmet } from 'react-helmet'
import LandingPage1 from 'Assets/images/LandingPage1.png'
import LandingPage2 from 'Assets/images/LandingPage2.png'

function Home() {
   const { Title, Paragraph } = Typography
   const TITLE = 'Home | EEG Classification'
   return (
      <>
         <Helmet>
            <title>{TITLE}</title>
         </Helmet>
         <Card style={{ marginBottom: '30px' }}>
            <Row>
               <Col span={6}>
                  <Image
                     src={LandingPage1}
                     preview={false}
                     style={{
                        margin: '50px 0 50px 100px',
                     }}
                  />
               </Col>
               <Col span={18} push={3} style={{
                  margin: '100px 0 50px 0',
               }}>
                  <Title>EEG Classification</Title>
                  <Title level={4} style={{
                     margin: '0',
                  }}>EEG signal classification using <br />Random Forest, K-Nearest Neighbor and XGBoost models</Title>

                  <Paragraph w style={{
                     margin: '10px 0',
                     width: '70%'
                  }}>To get the results of word classification in the form of combinations of basic words such as the words “Makan”, “Minum”, ”Lapar”, ”Haus”, ”Senang”, ”Sedih”, ”Sakit” and ”Toilet”  with condition words such as “Gambar”, “Baca”, “Hati” dan “Imajinasi” so as to produce word combinations such as the word “Makan-Gambar”.</Paragraph>
               </Col>
            </Row>
         </Card>

         <Card>
            <Row>
               <Col span={16} style={{
                  margin: '50px 0 50px 10px',
               }}>
                  <Title>Features used in the model</Title>
                  <Paragraph>EEG signal data recording using the emotiv epoc+ device. After the recording data has been taken, the FFT extraction process is carried out. This extraction process produces 640 lines of data extraction for each subject session and has 70 columns consisting of a combination of 14 electrodes and 5 waves, namely theta, alpha, low beta, high beta, gamma. The following features are used in the model: </Paragraph>

                  <Paragraph>AF3_THETA, AF3_ALPHA, AF3_LOW_BETA, AF3_HIGH_BETA, AF3_GAMMA, F7_THETA, F7_ALPHA, F7_LOW_BETA, F7_HIGH_BETA, F7_GAMMA, F3_THETA, F3_ALPHA, F3_LOW_BETA, F3_HIGH_BETA, F3_GAMMA, FC5_THETA, FC5_ALPHA, FC5_LOW_BETA, FC5_HIGH_BETA, FC5_GAMMA, T7_THETA, T7_ALPHA, T7_LOW_BETA, T7_HIGH_BETA, T7_GAMMA, P7_THETA, P7_ALPHA, P7_LOW_BETA, P7_HIGH_BETA, P7_GAMMA, O1_THETA, O1_ALPHA, O1_LOW_BETA, O1_HIGH_BETA, O1_GAMMA, O2_THETA, O2_ALPHA, O2_LOW_BETA, O2_HIGH_BETA, O2_GAMMA, P8_THETA, P8_ALPHA, P8_LOW_BETA, P8_HIGH_BETA, P8_GAMMA, T8_THETA, T8_ALPHA, T8_LOW_BETA, T8_HIGH_BETA, T8_GAMMA, FC6_THETA, FC6_ALPHA, FC6_LOW_BETA, FC6_HIGH_BETA, FC6_GAMMA, F4_THETA, F4_ALPHA, F4_LOW_BETA, F4_HIGH_BETA, F4_GAMMA, F8_THETA, F8_ALPHA, F8_LOW_BETA, F8_HIGH_BETA, F8_GAMMA, AF4_THETA, AF4_ALPHA, AF4_LOW_BETA, AF4_HIGH_BETA, AF4_GAMMA.</Paragraph>
               </Col>
               <Col span={6} push={1}>
                  <Image
                     src={LandingPage2}
                     preview={false}
                     style={{
                        margin: '100px 0 50px 20px',
                     }}
                  />
               </Col>
            </Row>
         </Card>
      </>
   )
}

export default Home
