import React from 'react';
import Breadcrumb from 'Elements/Breadcrumbs';
import { Button, Card, Col, Image, Row, Typography } from 'antd';
import { useNavigate } from 'react-router-dom';

import RandomForestTrain from 'Assets/images/random_forest/random_forest_train.png'
import RandomForestTest from 'Assets/images/random_forest/random_forest_test.png'
import RandomForestFeature from 'Assets/images/random_forest/random_forest_features_importance.png'
import RandomForestCM from 'Assets/images/random_forest/random_forest_cm.png'
import RandomForestCF from 'Assets/images/random_forest/random_forest_cf.png'
import Helmet from 'react-helmet';

function RandomForest() {
   const navigate = useNavigate()
   const TITLE = 'Random Forest | EEG Classification'

   const handleNavigate = (url) => {
      if (url === 'predict') navigate('/predict')
      else if (url === 'back') navigate('/predict-eeg')
   }

   const breadcrumb = [
      {
         pageTitle: "Predict EEG",
         pageHref: "/predict-eeg"
      },
      {
         pageTitle: "Random Forest",
         pageHref: "/random-forest"
      }
   ]

   const { Title } = Typography;

   return (
      <>
         <Helmet>
            <title>{TITLE}</title>
         </Helmet>
         <Breadcrumb data={breadcrumb} />
         <Title
            style={{
               textAlign: 'center',
               marginTop: '30px'
            }}
         >Random Forest</Title>
         <div style={{
            textAlign: 'center',
            marginBottom: '30px'
         }}>
            <Button type="primary" onClick={() => handleNavigate('predict')}>Start Prediction</Button>
            <Button type="primary" onClick={() => handleNavigate('back')} style={{ marginLeft: '5px' }}>Back</Button>
         </div>

         <Row
            style={{
               textAlign: 'center',
               margin: '10px auto'
            }}>
            <Col span={12}>
               <Row>
                  <Col span={12}>
                     <Title
                        level={3}
                        style={{
                           margin: '10px auto'
                        }}
                     >Classification Report for Data Train</Title>
                     <Image
                        src={RandomForestTrain}
                        width={'100%'}
                     />
                  </Col>
                  <Col span={12}>
                     <Title
                        level={3}
                        style={{
                           textAlign: 'center',
                           margin: '10px auto'
                        }}
                     >Classification Report for Data Test</Title>
                     <Image
                        src={RandomForestTest}
                        width={'100%'}
                     />
                  </Col>
               </Row>
            </Col>
            <Col span={9} offset={1}>
               <Card
                  style={{
                     borderRadius: '20px',
                  }}>
                  <Title
                     level={2}
                  >Information About This Model</Title>
                  <Title level={4} style={{
                     textAlign: 'left',
                     marginTop: '-15px'
                  }}>Random Forest</Title>
                  <Title level={5} style={{
                     textAlign: 'left',
                     marginTop: '5px'
                  }}>Parameter</Title>
                  <ul style={{
                     textAlign: 'left'
                  }}>
                     <li>N estimators: <b>500</b></li>
                     <li>Criterion: <b>entropy</b></li>
                     <li>Min samples split: <b>2</b></li>
                     <li>Min samples leaf: <b>1</b></li>
                  </ul>
                  <Title level={5} style={{
                     textAlign: 'left',
                     margin: 0
                  }}>Model Accuracy : 93%</Title>
                  <Title level={5} style={{
                     textAlign: 'left',
                     margin: 0
                  }}>Cross Validation : 5 Fold</Title>
               </Card>
            </Col>
         </Row>

         <Row>
            <Card>
               <div style={{
                  display: 'flex',
                  justifyContent: 'center',
                  margin: '10px auto'
               }}>
                  <Title
                     level={2}
                     style={{
                        textAlign: 'center'
                     }}
                  >Parameter Grid</Title>
                  <Image
                     src={RandomForestCF}
                     width={'100%'}
                  />
               </div>
            </Card>
         </Row>

         <Row>
            <Card
               style={{
                  display: 'flex',
                  justifyContent: 'center',
                  margin: '10px auto'
               }}>
               <Title
                  level={2}
                  style={{
                     textAlign: 'center'
                  }}
               >Confusion Matrix </Title>
               <Image
                  src={RandomForestCM}
                  width={'100%'}
               />
            </Card>
         </Row>

         <Row>
            <Card
               style={{
                  display: 'flex',
                  justifyContent: 'center',
                  margin: '30px auto'
               }}>
               <Title
                  level={2}
                  style={{
                     textAlign: 'center'
                  }}
               >Features Importance</Title>
               <Image
                  src={RandomForestFeature}
                  width={'100%'}
               />
            </Card>
         </Row>
      </>
   )
}

export default RandomForest