import React, { useContext, useState, useEffect } from 'react';
import Breadcrumb from 'Elements/Breadcrumbs';
import { Button, Card, Col, Row, Select, Typography, Form, Input } from 'antd';
import { useNavigate } from 'react-router-dom';
import { PredictContext } from 'Context/PredictContext';
import Helmet from 'react-helmet';

function Predict() {
   const navigate = useNavigate()
   const { electrodes, electodeName, functions } = useContext(PredictContext)
   const { singlePredict, blankSingleResult } = functions
   const [method, setMethod] = useState('Select Method')
   const [showInputs, setShowInputs] = useState(false)
   const { Option } = Select
   const TITLE = 'Input Features | EEG Classification'

   const breadcrumb = [
      {
         pageTitle: "Predict EEG",
         pageHref: "/predict-eeg"
      },
      {
         pageTitle: "Predict",
         pageHref: "/predict"
      }
   ]

   const handleNavigate = () => {
      navigate('/upload-csv')
   }

   const onFinish = (values) => {
      let method = values.method
      delete values.method
      let data = values
      try {
         singlePredict(data, method)
         navigate('/predict-result')
      } catch (error) {
         console.log(error);
      }
   };

   const onFinishFailed = () => {
      console.log('Failed');
   };

   useEffect(() => {
      blankSingleResult()
      // eslint-disable-next-line react-hooks/exhaustive-deps
   }, [])


   const { Title } = Typography;

   const onMentodChange = (value) => {
      setShowInputs(true)
      switch (value) {
         case 'random-forest':
            setMethod('Random Forest');
            return;
         case 'k-nearest-neigbor':
            setMethod('K Nearest Neighbor');
            return;
         case 'XGboost':
            setMethod('Xtreme Gradient Boost');
            return;
         default:
            setMethod('Pick a Method');
            return;
      }
   };

   const Gridstyle = { background: '#bce2ea', padding: '8px 0', marginBottom: '10px', borderRadius: '10px' };
   const Inputstyle = { background: '#d6e9ef', borderRadius: '10px' };
   const FormItemstyle = { marginBottom: '2px' };

   return (
      <>
         <Helmet>
            <title>{TITLE}</title>
         </Helmet>
         <Breadcrumb data={breadcrumb} />

         <div
            style={{
               display: 'flex',
               justifyContent: 'space-between',
               margin: '30px 30px '
            }}
         >
            <Title>Start Prediction</Title>
            <Button type='primary' onClick={handleNavigate}>Predict from CSV File</Button>
         </div>
         <div
            style={{
               display: 'flex',
               justifyContent: 'center',
            }}
         >
            <Title level={3}>Method : {method}</Title>
         </div>

         <Form
            labelCol={{ span: 16 }}
            wrapperCol={{ span: 24 }}
            initialValues={{ remember: true }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
            layout="vertical"
         >
            <Form.Item>
               <Button type="primary" htmlType="submit">
                  Submit Features
               </Button>
            </Form.Item>

            <Form.Item name="method" label="Method" rules={[{ required: true, message: `Please input method field!` }]}>
               <Select
                  placeholder="Select a method"
                  onChange={onMentodChange}
                  allowClear
               >
                  <Option value="random-forest">Random Forest</Option>
                  <Option value="k-nearest-neigbor">K Nearest Neigbor</Option>
                  <Option value="XGboost">Extreme Gradient Boost</Option>
               </Select>
            </Form.Item>

            {showInputs &&
               <>
                  <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                     {electrodes.map((electrode, indexElectrode) => (
                        <Col span={6} key={indexElectrode}>
                           <Card
                              style={Gridstyle}>
                              <h2>{electodeName[indexElectrode]}</h2>
                              {Object.keys(electrode).map((item, index) => (
                                 <Form.Item
                                    key={"field " + index}
                                    label={item}
                                    name={item}
                                    style={FormItemstyle}
                                    rules={[{ required: true, message: `Please input ${item} field!` }]}
                                 >
                                    <Input type={'number'} style={Inputstyle} />
                                 </Form.Item>
                              ))}
                           </Card>
                        </Col>
                     ))}
                  </Row>
               </>}
         </Form>
      </>
   );
}

export default Predict;