import React from 'react';
import { Route, Routes } from 'react-router-dom';

import Home from 'Pages/Home';
import PredictEEG from 'Pages/PredictEEG';
import PredictResult from 'Pages/PredictResult';
import Predict from 'Pages/Predict';
import UploadCSV from 'Pages/UploadCSV';
import MultiplePredictResult from 'Pages/MultiplePredictResult';
import RecentPredict from 'Pages/RecentPredict';
import PredictDetails from 'Pages/PredictDetails';
import RandomForest from 'Pages/RandomForest';
import Knn from 'Pages/Knn';
import Xgb from 'Pages/Xgb';

function AppRoutes() {
   return (
      <Routes>
         <Route path="/" element={<Home />}></Route>
         <Route path="/predict-eeg" element={<PredictEEG />}></Route>
         <Route path="/predict" element={<Predict />}></Route>
         <Route path="/upload-csv" element={<UploadCSV />}></Route>
         <Route path="/predict-result" element={<PredictResult />}></Route>
         <Route path="/multiple-predict-result" element={<MultiplePredictResult />}></Route>
         <Route path="/recent-predict" element={<RecentPredict />}></Route>
         <Route path="/predict-details" element={<PredictDetails />}></Route>

         <Route path="/random-forest" element={<RandomForest />}></Route>
         <Route path="/knn" element={<Knn />}></Route>
         <Route path="/xgboost" element={<Xgb />}></Route>
      </Routes>
   );
}

export default AppRoutes;
