import { PredictContext } from 'Context/PredictContext'
import Breadcrumb from 'Elements/Breadcrumbs'
import { Button, Card, Col, Row, Table, Tag, Typography } from 'antd'
import React, { useContext, useEffect, useState } from 'react'

import {
   SearchOutlined,
   DeleteOutlined,
} from '@ant-design/icons'
import Helmet from 'react-helmet'

function RecentPredict() {
   const { predictList, functions } = useContext(PredictContext)
   const { fetchData, fetchSingleData, deleteData } = functions
   const { Title } = Typography;
   const [page, setPage] = useState(1)
   const [pageSize, setPageSize] = useState(20)
   const TITLE = 'Recent Predict | EEG Classification'

   useEffect(() => {
      fetchData()
      // eslint-disable-next-line react-hooks/exhaustive-deps
   }, [])

   const handleDetails = (idPredict) => {
      let id = parseInt(idPredict)
      fetchSingleData(id)
   }

   const handleDelete = (idGames) => {
      let id = parseInt(idGames)
      deleteData(id);
   }

   const Gridstyle = { background: '#bce2ea', padding: '8px 0', marginBottom: '10px', borderRadius: '10px' };
   const LineStyle = {
      display: 'flex',
      justifyContent: 'space-between',
      margin: '30px 30px 0 30px '
   }

   const columns = [
      {
         title: 'Word',
         dataIndex: 'word',
         key: 'word',
         sorter: (a, b) => a.word > b.word,
      },
      {
         title: 'Condition',
         dataIndex: 'condition',
         key: 'condition',
         sorter: (a, b) => a.condition > b.condition,
      },
      {
         title: 'Created',
         dataIndex: 'created',
         key: 'created',
         sorter: (a, b) => a.created > b.created,
      },
      {
         title: 'Method',
         dataIndex: 'method',
         key: 'method',
         render: (method) => {
            if (method === 'random-forest') {
               return (
                  <Tag color={'green'}>Random Forest</Tag>
               )
            } else if (method === 'k-nearest-neigbor') {
               return (
                  <Tag color={'volcano'}>K Nearest Neigbor</Tag>
               )
            } else if (method === 'Xgboost') {
               return (
                  <Tag color={'yellow'}>Xtreme Gradient Boost</Tag>
               )
            }
         },
         filters: [
            { text: 'Random Forest', value: 'random-forest' },
            { text: 'K Nearest Neigbor', value: 'k-nearest-neigbor' },
            { text: 'Xtreme Gradient Boost', value: 'Xgboost' }
         ],
         onFilter: (value, record) => {
            // console.log(record)
            return record.method === value
         }
      },
      {
         title: 'Action',
         dataIndex: 'id',
         key: 'id',

         render: (id) => {
            return (
               <>
                  <Button type='primary' ghost style={{ marginRight: '5px' }} onClick={() => handleDetails(id)}><SearchOutlined /></Button>
                  <Button type='danger' onClick={() => handleDelete(id)}><DeleteOutlined /></Button>
               </>
            )
         }
      },
   ]

   const breadcrumb = [
      {
         pageTitle: "Recent Prediction",
         pageHref: "/recent-predict"
      },
   ]

   return (
      <>
         <Helmet>
            <title>{TITLE}</title>
         </Helmet>
         <Breadcrumb data={breadcrumb} />
         <div style={LineStyle}>
            <Title>Recent Prediction</Title>
         </div>

         <Row>
            <Col span={16} style={{ marginRight: '30px' }}>
               <Card
                  style={Gridstyle}>
                  <Table
                     rowKey="id"
                     dataSource={predictList}
                     columns={columns}
                     pagination={{
                        current: page,
                        pageSize: pageSize,
                        onChange: (page, pageSize) => {
                           setPage(page)
                           setPageSize(pageSize)
                        }
                     }}
                  />
               </Card>
            </Col>
            <Col span={7}>
               <Card>
                  <Title level={3}>Note</Title>
                  <Title level={4}>Retrieve the last 100 data from the classification results.</Title>
               </Card>
            </Col>
         </Row>
      </>
   );
}

export default RecentPredict;
