import React from 'react';
import Breadcrumb from 'Elements/Breadcrumbs';
import { Button, Card, Col, Image, Row, Typography } from 'antd';
import { useNavigate } from 'react-router-dom';

import KnnTrain from 'Assets/images/knn/knn_train.png'
import KnnTest from 'Assets/images/knn/knn_test.png'
import KnnCM from 'Assets/images/knn/knn_cm.png'
import KnnCF from 'Assets/images/knn/knn_cf.png'
import Helmet from 'react-helmet';

function Knn() {
   const navigate = useNavigate()

   const handleNavigate = (url) => {
      if (url === 'predict') navigate('/predict')
      else if (url === 'back') navigate('/predict-eeg')
   }

   const breadcrumb = [
      {
         pageTitle: "Predict EEG",
         pageHref: "/predict-eeg"
      },
      {
         pageTitle: "K Nearest Neigbor",
         pageHref: "/knn"
      }
   ]

   const { Title } = Typography
   const TITLE = 'K-Nearest Neigbor | EEG Classification'

   return (
      <>
         <Helmet>
            <title>{TITLE}</title>
         </Helmet>
         <Breadcrumb data={breadcrumb} />
         <Title
            style={{
               textAlign: 'center',
               marginTop: '30px'
            }}
         >K Nearest Neigbor</Title>
         <div style={{
            textAlign: 'center',
            marginBottom: '30px'
         }}>
            <Button type="primary" onClick={() => handleNavigate('predict')}>Start Prediction</Button>
            <Button type="primary" onClick={() => handleNavigate('back')} style={{ marginLeft: '5px' }}>Back</Button>
         </div>

         <Row
            style={{
               textAlign: 'center',
               margin: '10px auto'
            }}>
            <Col span={12}>
               <Row>
                  <Col span={12}>
                     <Title
                        level={3}
                        style={{
                           margin: '10px auto'
                        }}
                     >Classification Report for Data Train</Title>
                     <Image
                        src={KnnTrain}
                        width={'100%'}
                     />
                  </Col>
                  <Col span={12}>
                     <Title
                        level={3}
                        style={{
                           textAlign: 'center',
                           margin: '10px auto'
                        }}
                     >Classification Report for Data Test</Title>
                     <Image
                        src={KnnTest}
                        width={'100%'}
                     />
                  </Col>
               </Row>
            </Col>
            <Col span={9} offset={1}>
               <Card
                  style={{
                     borderRadius: '20px',
                  }}>
                  <Title
                     level={2}
                  >Information About This Model</Title>
                  <Title level={4} style={{
                     textAlign: 'left',
                     marginTop: '-15px'
                  }}>K Nearest Neigbor</Title>
                  <Title level={5} style={{
                     textAlign: 'left',
                     marginTop: '5px'
                  }}>Parameter</Title>
                  <ul style={{
                     textAlign: 'left'
                  }}>
                     <li>N Neigbors: <b>1</b></li>
                     <li>Weight: <b>Distance</b></li>
                     <li>P: <b>1</b></li>
                  </ul>
                  <Title level={5} style={{
                     textAlign: 'left',
                     margin: 0
                  }}>Model Accuracy : 74%</Title>
                  <Title level={5} style={{
                     textAlign: 'left',
                     margin: 0
                  }}>Cross Validation : 5 Fold</Title>
               </Card>
            </Col>
         </Row>

         <Row>
            <Card>
               <div style={{
                  display: 'flex',
                  justifyContent: 'center',
                  margin: '10px auto'
               }}>
                  <Title
                     level={2}
                     style={{
                        textAlign: 'center'
                     }}
                  >Parameter Grid</Title>
                  <Image
                     src={KnnCF}
                     width={'100%'}
                  />
               </div>
            </Card>
         </Row>

         <Row>
            <Card
               style={{
                  display: 'flex',
                  justifyContent: 'center',
                  margin: '10px auto'
               }}>
               <Title
                  level={2}
                  style={{
                     textAlign: 'center'
                  }}
               >Confusion Matrix </Title>
               <Image
                  src={KnnCM}
                  width={'100%'}
               />
            </Card>
         </Row>
      </>
   )
}

export default Knn